import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import {
  fetchGenre,
  fetchMovieByGenre,
  fetchPersons,
  fetchTopratedMovie,
} from "../../../Config";
import ReactStars from "react-rating-stars-component";
import "./Landing.css";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";

export function MultiCarousel() {
  const [genres, setGenres] = useState([]);
  const [movieByGenre, setMovieByGenre] = useState([]);
  const [persons, setPersons] = useState([]);
  const [topRated, setTopRated] = useState([]);

  useEffect(() => {
    const fetchAPI = async () => {
      setGenres(await fetchGenre());
      setMovieByGenre(await fetchMovieByGenre(28));
      setPersons(await fetchPersons());
      setTopRated(await fetchTopratedMovie());
    };
    fetchAPI();
  }, []);

  const handleGenreClick = async (genre_id) => {
    setMovieByGenre(await fetchMovieByGenre(genre_id));
  };

  const genreList = genres.map((item, index) => {
    return (
      <li className="list-inline-item" key={index}>
        <button
          style={{ wordWrap: "normal", verticalAlign: "baseline" }}
          type="button"
          className="btn btn-outline-info"
          onClick={() => {
            handleGenreClick(item.id);
          }}
        >
          {item.name}
        </button>
      </li>
    );
  });

  const movieList = movieByGenre.slice(0, 20).map((item, index) => {
    return (
      <div key={index}>
        <Card className="bg-dark text-white">
          {" "}
          <Card.Img src={item.poster} alt={item.title} />
          <Card.ImgOverlay className="card__content">
            <h5>{item.title}</h5>
            <Button as={Link} to={`/movie/${item.id}`} variant="outline-info">
              MORE
            </Button>
          </Card.ImgOverlay>
        </Card>

        <ReactStars
          count={10}
          value={item.rating}
          isHalf={true}
          edit={false}
        ></ReactStars>
        <br />
      </div>
    );
  });

  const trendingPersons = persons.slice(0, 20).map(
    (p, i) =>
      p.profileImg !== "https://image.tmdb.org/t/p/w200null" && (
        <div key={i}>
          <Card
            className="bg-transparent text-white"
            style={{ border: "none" }}
          >
            <Card.Img
              className="img-fluid rounded-circle mx-auto d-block"
              src={p.profileImg}
              alt={p.name}
            />
            <Card.ImgOverlay className="card__content"></Card.ImgOverlay>
          </Card>
          <p className="font-weight-bold text-center">{p.name}</p>
        </div>
      )
  );

  const topRatedList = topRated.slice(0, 20).map((item, index) => {
    return (
      <div key={index}>
        <Card className="bg-dark text-white">
          <Card.Img src={item.poster} alt={item.title} />
          <Card.ImgOverlay className="card__content">
            {" "}
            <h5>{item.title}</h5>
            <Button as={Link} to={`/movie/${item.id}`} variant="outline-info">
              MORE
            </Button>
          </Card.ImgOverlay>
        </Card>

        <ReactStars
          count={10}
          value={item.rating}
          isHalf={true}
          edit={false}
        ></ReactStars>
        <br />
      </div>
    );
  });

  const responsive = {
    superLargeDesktop: {
      // the naming can be any, depends on you.
      breakpoint: { max: 4000, min: 3000 },
      items: 5,
      slidesToSlide: 5,
    },
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 4,
      slidesToSlide: 4,
      partialVisibilityGutter: 40, // this is needed to tell the amount of px that should be visible.
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 3,
      slidesToSlide: 3,
      partialVisibilityGutter: 30, // this is needed to tell the amount of px that should be visible.
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 2,
      slidesToSlide: 2,
      partialVisibilityGutter: 30, // this is needed to tell the amount of px that should be visible.
    },
  };

  const responsiveActor = {
    desktop: {
      breakpoint: { max: 4000, min: 1024 },
      items: 5,
      slidesToSlide: 5,
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 4,
      slidesToSlide: 4,
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 3,
      slidesToSlide: 3,
    },
  };

  return (
    <>
      <div className="row mt-3">
        <div className="col multi__carousel">
          <h3>Movie by Genre</h3>
          <ul className="list-inline">{genreList}</ul>{" "}
          <Carousel
            // arrows={false}
            showDots={true}
            responsive={responsive}
            // partialVisible={true}
            centerMode={true}
            infinite={true}
            minimumTouchDrag={20}
            removeArrowOnDeviceType="mobile"
            dotListClass="dots"
          >
            {movieList}
          </Carousel>{" "}
          <h3>Trending Persons</h3>
          <Carousel
            arrows={false}
            showDots={true}
            responsive={responsiveActor}
            // partialVisible={true}
            // centerMode={true}
            infinite={true}
            minimumTouchDrag={20}
            removeArrowOnDeviceType="mobile"
            dotListClass="dots"
          >
            {trendingPersons}
          </Carousel>
          <h3>Top Rated Movies</h3>
          <Carousel
            // arrows={false}
            showDots={true}
            responsive={responsive}
            // partialVisible={true}
            centerMode={true}
            infinite={true}
            minimumTouchDrag={20}
            removeArrowOnDeviceType="mobile"
            dotListClass="dots"
          >
            {topRatedList}
          </Carousel>
        </div>
      </div>
    </>
  );
}

export default MultiCarousel;
