import React, { useState, useEffect } from "react";
import {
  fetchMovieDetail,
  fetchMovieVideos,
  fetchSimilarMovie,
  fetchCasts,
} from "../../Config";
import { Modal } from "react-bootstrap";
import ReactPlayer from "react-player";
import ReactStars from "react-rating-stars-component";
import { Link } from "react-router-dom";
import Button from "react-bootstrap/Button";
import axios from "axios";
import Comments from "./Sections/Comments";
import Favorite from "./Sections/Favorite";
import LikeDislikes from "./Sections/LikeDislikes";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import Card from "react-bootstrap/Card";
import { Icon } from "antd";
import Jumbotron from "react-bootstrap/Jumbotron";
import "./Detail.css";
import responsive from "../LandingPage/Sections/MultiCarousel";

function MovieVideo({ match }) {
  let params = match.params;
  let genres = [];
  const [isOpen, setIsOpen] = useState(false);
  const [detail, setDetail] = useState([]);
  const [video, setVideo] = useState([]);
  const [casts, setCasts] = useState([]);
  const [similarMovie, setSimilarMovie] = useState([]);
  const [ActorToggle, setActorToggle] = useState(false);
  const [CommentLists, setCommentLists] = useState([]);
  const movieVariable = {
    movieId: params.id,
  };

  useEffect(() => {
    axios.post("/api/comment/getComments", movieVariable).then((response) => {
      console.log(response);
      if (response.data.success) {
        console.log("response.data.comments", response.data.comments);
        setCommentLists(response.data.comments);
      } else {
        alert("Failed to get comments Info");
      }
    });
  }, []);

  const updateComment = (newComment) => {
    setCommentLists(CommentLists.concat(newComment));
  };

  useEffect(() => {
    const fetchAPI = async () => {
      setDetail(await fetchMovieDetail(params.id));
      setVideo(await fetchMovieVideos(params.id));
      setCasts(await fetchCasts(params.id));
      setSimilarMovie(await fetchSimilarMovie(params.id));
    };
    fetchAPI();
    window.scrollTo(0, 0);
  }, [params.id]);

  genres = detail.genres;
  const toggleActorView = () => {
    setActorToggle(!ActorToggle);
  };

  const [show, handleShow] = useState(false);

  useEffect(() => {
    window.addEventListener("scroll", () => {
      if (window.innerHeight + window.scrollY >= document.body.scrollHeight) {
        handleShow(true);
      } else handleShow(false);
    });
    return () => {
      window.removeEventListener("scroll", MovieVideo, true);
    };
  }, []);

  const MoviePlayerModal = (props) => {
    const youtubeUrl = "https://www.youtube.com/watch?v=";

    return (
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton style={{ background: "#000" }}>
          <Modal.Title
            id="contained-modal-title-vcenter"
            style={{ fontWeight: "bolder" }}
          >
            {detail.title}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ backgroundColor: "#000" }}>
          <ReactPlayer
            className="container-fluid"
            url={
              video
                ? youtubeUrl + video.key
                : "https://www.youtube.com/watch?v=8tPnX7OPo0Q"
            }
            playing
            controls={true}
            width="100%"
          ></ReactPlayer>
        </Modal.Body>
      </Modal>
    );
  };

  let genresList;
  if (genres) {
    genresList = genres.map((g, i) => {
      return (
        <li className="list-inline-item genre" key={i}>
          <Button variant="outline-primary" size="lg" disabled>
            {g.name}
          </Button>
        </li>
      );
    });
  }

  const castList = casts.map(
    (c, i) =>
      c.img !== "https://image.tmdb.org/t/p/w200null" && (
        <div className="col-md-3  col-xs-6 text-center" key={i}>
          <img
            className="img-fluid rounded-circle mx-auto d-block"
            src={c.img}
            onError="no image"
            alt={c.name}
          ></img>
          <p className="font-weight-bold text-center">{c.name}</p>
          <p className=" text-center" style={{ color: "#5a606b" }}>
            {c.character}
          </p>
        </div>
      )
  );
  const similarMovieList = similarMovie.slice(0, 20).map((item, index) => {
    return (
      <div key={index}>
        <Card className="bg-dark text-white">
          {" "}
          <Card.Img src={item.poster} alt={item.title} />
          <Card.ImgOverlay className="card__content">
            <Button variant="outline-info">
              {" "}
              <a href={`/movie/${item.id}`} style={{ textDecoration: "none" }}>
                MORE
              </a>
            </Button>
          </Card.ImgOverlay>
        </Card>

        <ReactStars
          count={10}
          value={item.rating}
          isHalf={true}
          edit={false}
        ></ReactStars>
        <br />
      </div>
    );
  });
  const responsive = {
    superLargeDesktop: {
      // the naming can be any, depends on you.
      breakpoint: { max: 4000, min: 3000 },
      items: 5,
      slidesToSlide: 5,
    },
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 4,
      slidesToSlide: 4,
      partialVisibilityGutter: 40, // this is needed to tell the amount of px that should be visible.
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 3,
      slidesToSlide: 3,
      partialVisibilityGutter: 30, // this is needed to tell the amount of px that should be visible.
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 2,
      slidesToSlide: 2,
      partialVisibilityGutter: 30, // this is needed to tell the amount of px that should be visible.
    },
  };

  return (
    <div className="container">
      <div className="row mt-2" id="start">
        <MoviePlayerModal
          show={isOpen}
          onHide={() => {
            setIsOpen(false);
          }}
        ></MoviePlayerModal>
        <Jumbotron
          fluid
          style={{
            backgroundSize: "cover",
            backgroundImage: `url("https://image.tmdb.org/t/p/original/${
              detail.backdrop_path ? detail.backdrop_path : detail.poster_path
            }")`,
            backgroundPosition: "center center",
            height: "30vmax",
          }}
          className="col text-center"
        >
          <Button
            variant="outline-primary"
            onClick={() => setIsOpen(true)}
            className="play"
          >
            <svg
              width="1em"
              height="1em"
              viewBox="0 0 16 16"
              className="bi bi-play"
              fill="currentColor"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fillRule="evenodd"
                d="M10.804 8L5 4.633v6.734L10.804 8zm.792-.696a.802.802 0 0 1 0 1.392l-6.363 3.692C4.713 12.69 4 12.345 4 11.692V4.308c0-.653.713-.998 1.233-.696l6.363 3.692z"
              />
            </svg>
          </Button>
          <div>
            <h3>{detail.title}</h3>
            <b>
              {detail.release_date}
              <span> | {detail.runtime} mins | </span>IMDB {detail.vote_average}
            </b>
            <p>{detail.tagline}</p>
            <div style={{ whiteSpace: "nowrap", padding: "0" }}>
              {" "}
              <Favorite
                movieInfo={detail}
                movieId={params.id}
                userFrom={localStorage.getItem("userId")}
              />{" "}
              <Button
                variant="outline-primary"
                onClick={() => setIsOpen(true)}
                className="play__2"
              >
                Play Trailer
              </Button>
            </div>
          </div>
        </Jumbotron>
      </div>
      <div className="row mt-3">
        <div className="col">
          <ul className="list-inline">{genresList}</ul>
        </div>
      </div>
      <div className="row mt-3">
        <div className="col">
          <div className="mt-3">
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <h3>Overview</h3>
              <a
                href={detail.homepage}
                target="_blank"
                rel="noopener noreferrer"
              >
                <Button style={{ padding: "3px 3px" }}>
                  Go to Homepage{" "}
                  <span role="img" aria-label="arrow_forward">
                    
                  </span>
                </Button>
              </a>
            </div>

            <b>{detail.title}</b>
            <p>{detail.overview}</p>
          </div>
        </div>
      </div>
      <LikeDislikes
        video
        videoId={params.id}
        userId={localStorage.getItem("userId")}
      />
      <div
        style={{ display: "flex", justifyContent: "center", margin: "2rem" }}
      >
        <Button onClick={toggleActorView}>See Casts </Button>
      </div>
      {ActorToggle && <div className="row mt-3">{castList}</div>}
      <div className="row mt-3">
        <div className="col">
          <h3>Similar Movies</h3>
        </div>
      </div>
      <Carousel
        // arrows={false}
        showDots={true}
        responsive={responsive}
        // partialVisible={true}
        centerMode={true}
        infinite={true}
        minimumTouchDrag={20}
        removeArrowOnDeviceType="mobile"
        dotListClass="dots"
      >
        {similarMovieList}
      </Carousel>{" "}
      <Comments
        movieTitle={detail.title}
        CommentLists={CommentLists}
        postId={params.id}
        refreshFunction={updateComment}
      />
      <a href="#start" className={`${show && "showStart"} hideStart`}>
        <Icon type="arrow-up" />
      </a>
      {/* <Button className={`${show && "showStart"} hideStart`}>^</Button> */}
    </div>
  );
}

export default MovieVideo;
